

import { useParams } from 'react-router-dom';
import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import PropTypes from 'prop-types';
import { getApiResourse } from '../../utils/network';
import { getPeopleImage } from '../../services/getPeopleData';
import UiLoading from '@ui/UiLoading';
import PersonLinkBack from '../../components/PersonPage/PersonLinkBack/PersonLinkBack';
import { withErrorApi } from '@hoc-helpers/withErrorApi';
import iconFavorite from './img/favorite.svg';
import iconFavoriteFill from './img/favorite-fill.svg';
import PersonPhotoVlas from '../PersonPage/PersonPhotoVlas/PersonPhotoVlas';

import s from './VlasovPers.module.css';



const VlasovPers = (setErrorApi) => {


  const [personId, setPersonId] = useState(null);
  const [persName, setPersonName] = useState(null);
  const [photo, setPersonPhoto] = useState(null);
  const [personFavorite, setPersonFavorite] = useState(false);

  const storeData = useSelector(state => state.favoriteReducer);


  const params = useParams();
  let id = params.id;
  
  

  // setPersonId(id);


  useEffect(() => {

    (async () => {

      setPersonFavorite(!!storeData[id]);
      setPersonId(id);

      // const res = await getApiResourse(`${API_PERSON}/${id}/`);
      const res = await getApiResourse(`https://api.tvmaze.com/people`);
      //const res = await getApiResourse(`https://bymykel.github.io/CSGO-API/api/skins.json`)

      if (res) {


        let hoto = res[id - 1].image.original
        let persN = res[id - 1].name

        setPersonName(persN)
        setPersonPhoto(hoto)
        // setPersonPhoto(getPeopleImage(id))
        // setErrorApi(false)
      }
      // else {
      //   setErrorApi(true)
      // }


    })()

  }, [])



  return (
    <>
      <PersonLinkBack />

      <div className={s.wrapper}>
        <span className={s.person__name}>{persName}</span>
        <div className={s.container}>

          {/* <img  className={s.photo} src={photo} alt={photo} />
                
                <img
                    src={personFavorite ? iconFavoriteFill : iconFavorite}
                    // onClick={dispatchFavoritePeople}
                    className={s.favorite}
                    alt="Add to favorite"
                /> */}


          <PersonPhotoVlas
            personId={personId}
            photo={photo}
            persName={persName}
            personFavorite={personFavorite}
            setPersonFavorite={setPersonFavorite}
          />

        </div>
      </div>
      
    </>
  )
}

// PersonPage.propTypes = {
//   setErrorApi: PropTypes.func,
// }

// export default withErrorApi(PersonPage);

export default VlasovPers;
