import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';

import { setPersonToFavorite, removePersonFromFavorites } from '@store/actions';

import iconFavorite from './img/favorite.svg';
import iconFavoriteFill from './img/favorite-fill.svg';

import styles from './PersonPhotoVlas.module.css';

const PersonPhotoVlas = (
    {
        personId,
        photo,
        persName,
        personFavorite,
        setPersonFavorite
    }
) => {

    
    const dispatch = useDispatch();

    const dispatchFavoritePeople = () => {
        if (personFavorite) {
            dispatch(removePersonFromFavorites(personId));
        } else {
            dispatch(setPersonToFavorite({
                [personId]: {
                    name: persName,
                    img: photo
                }
            }));
        }

        setPersonFavorite(!personFavorite);
    }

    return (
        <>
            <div className={styles.container}>
                
                <img className={styles.photo} src={photo} alt={persName} />
                <img
                    src={personFavorite ? iconFavoriteFill : iconFavorite}
                    onClick={dispatchFavoritePeople}
                    className={styles.favorite}
                    alt="Add to favorite"
                />
            </div>
        </>
    )
}

