import PropTypes from 'prop-types';
import { useState, useEffect, useCallback } from 'react';
import { debounce } from 'lodash';

import { withErrorApi } from '@hoc-helpers/withErrorApi';
import UiInput from '@ui/UiInput';
import SearchPageInfo from '@components/SearchPage/SearchPageInfo';

import { getApiResourse } from '@utils/network';
import { getPeopleImage, getPeopleId } from '@services/getPeopleData';
import { API_SEARCH } from '@constants/api';
import { VList } from '../../components/Vlasov/Vlasov';

import styles from './SearchPage.module.css';

let paramV;
let peopleList;

const SearchPage = ({ setErrorApi }) => {
    const [inputSearchValue, setInputSearchValue] = useState('');
    const [people, setPeople] = useState([]);




    const getResponse = async param => {
        //const res = await getApiResourse(API_SEARCH + param);
        const res = await getApiResourse('https://api.tvmaze.com/people');


        if (res) {
            const peopleList = res.filter(e => e.id < 10 && e.name && e.image)
                .map(({ id, name, image: { medium } }) => {
                    const img = { medium }
                    return {
                        id,
                        name,
                        img
                    }
                })
        }




        //const peopleList = res.map(({ name, url }) => {
        if (paramV) {
            peopleList = res
                //.filter(e => e.name==paramV&&e.image)
                .filter(e => e.name.toLowerCase().match(paramV.toLowerCase()) && e.image)
                .map(({ id, name, image: { medium } }) => {
                    //const id = getPeopleId(url);
                    //const img = getPeopleImage(id);
                    const img = { medium }
                    return {
                        id,
                        name,
                        img
                    }
                }
                );

            setPeople(peopleList);
        }

        setErrorApi(!res);
    };

    useEffect(() => {
        getResponse('');
    }, []);

    const debouncedGetResponse = useCallback(
        debounce(value => getResponse(value), 100),
        []
    );

    const handleInputChange = value => {
        setInputSearchValue(value);
        debouncedGetResponse(value);
        paramV = value
    }


    return (
        <>

            <h1 className="header__text">Search</h1>

            <UiInput
                value={inputSearchValue}
                handleInputChange={handleInputChange}
                placeholder="Input character's name"
                classes={styles.input__search}
            />

            <SearchPageInfo people={people} />
        </>
    )
}

SearchPage.propTypes = {
    setErrorApi: PropTypes.func,
}

export default withErrorApi(SearchPage);
